import React from 'react';
import {Link} from 'react-router-dom';
import '../global.css';
import '../App.css';
import logo from '../images/Logo.png';


function Menu() {
  return(
    <div className="App" >
      <main>
        <Link to="/sevengalaxies-app/Game"> 
          <img src={logo} alt="Seven Galaxies" width="80%"/>
        </Link>      
      </main>
    </div>
  )
}

export default Menu;