import React, { useState, useEffect} from 'react';
import '../global.css';
import '../App.css';


function Game() {
  const [rodada, setRodada] = useState({ _id: 1, nivel: 1, energia: 1 });
  //const [log,setLog] = useState([rodada]);
  const [escolher,setEscolher] = useState(false); 
  const [nivelMovel, setNivelMovel] = useState(1);
  const [nivelConquista, setNivelConquista] = useState(1);

  const [energiaMovel, setEnergiaMovel] = useState(1);
  const [energiaConquista, setEnergiaConquista] = useState(1);

  const [fortaleza, setFortaleza] = useState(30);

  /*useEffect(() => { 
    setNivelMovel(atual => atual + 1);
  },[nivelConquista]);

  useEffect(() => { 
    setEnergiaMovel( atual => atual + 1);
  },[energiaConquista]);*/
  function AlterConquista(indicador, valor){
    if (indicador ==='nivel') {
      setNivelMovel(atual=> atual + valor);
      setNivelConquista(atual=> atual + valor);

    }else{
      setEnergiaMovel(atual=> atual + valor);
      setEnergiaConquista(atual=> atual + valor);
    }

  }
  function newRodada(upNivel, upEnergia) {
    let nivelNew    = nivelConquista;
    let energiaNew  = energiaConquista;

    if (upNivel) 
      nivelNew++; 
    if (upEnergia)
      energiaNew++;

    setNivelMovel(nivelNew);
    setNivelConquista(nivelNew);

    setEnergiaMovel( energiaNew);
    setEnergiaConquista(energiaNew);

    setRodada({
      _id:      rodada._id + 1,
      nivel:    nivelNew,
      energia:  energiaNew});

    setEscolher(false);
  }
  
  function RodadaControl() {
    console.log("RodadaControl " + rodada._id);

    if (rodada._id + 1  <= 2)
      newRodada(true,true);
    else 
      setEscolher(true);
    
    console.log("RodadaControlss " + escolher);

  } 
  
  //seEffect(() => {    
  //console.log("useEffect " + escolher);
    //console.log("rodada " + rodada._id);
    //console.log("escolha " + escolher);  
    /*setRodada({
      _id:      rodada._id,
      nivel:    nivelConquista,
      energia:  energiaConquista});*/
    
  //},[/*rodada._id,nivelConquista,energiaConquista*/]);

  
  

  return (


    <main className="list-Reserva">
      <div className="Rodada">
        {escolher 
          ? <div className="Rodada-escolha" >
              <button onClick={() => newRodada(true,false)}> + Nivel </button> 
              <button onClick={() => newRodada(false,true)}> + Energia </button> 
            </div>
          : <button onClick={RodadaControl}> Nova Rodada </button>
        }
         <div className="Rodada-count" >
          <p> {rodada._id} </p> 
          </div> 
      </div>

      <div className="Reserva" >
        <header>
          <strong> NÃ­vel (N) </strong>
        </header>
        <div className="Reserva-movel">
          <button onClick={() => setNivelMovel(nivelMovel - 1)} > - </button>
          <p> {nivelMovel} </p>
          <button onClick={() => setNivelMovel(nivelMovel + 1)}> + </button>
        </div>
        <div className="Reserva-conquista">
          <button onClick={() => AlterConquista('nivel',- 1)} > - </button>
          <p> {nivelConquista} </p>
          <button onClick={() => AlterConquista('nivel', 1)}> + </button>
        </div>
      </div>

      <div className="Reserva" >
        <header>
          <strong> Energia (E) </strong>
        </header>
        <div className="Reserva-movel">
          <button onClick={() => setEnergiaMovel(energiaMovel - 1)} > - </button>
          <p> {energiaMovel} </p>
          <button onClick={() => setEnergiaMovel(energiaMovel + 1)}> + </button>
        </div>
        <div className="Reserva-conquista">
          <button onClick={() => AlterConquista('energia',- 1)} > - </button>
          <p> {energiaConquista} </p>
          <button onClick={() => AlterConquista('energia', 1)}> + </button>
        </div>
      </div>

      <div className="Reserva" >
        <header>
          <strong> Fortaleza </strong>
        </header>
        <div className="Reserva-movel">
          <button onClick={() => setFortaleza(fortaleza - 1)} > - </button>
          <p> {fortaleza} </p>
          <button onClick={() => setFortaleza(fortaleza + 1)}> + </button>
        </div>
      </div>


    </main>

  )
}

export default Game;